<template>
  <div id="project">
    <el-breadcrumb separator=">" class="zhTitle">
      项目列表
    </el-breadcrumb>
    <ul>
      <li  v-for="(item,index) in items" @click="showName(index)" class="zh-project-detail">
        <router-link :to="'/project/'+index" tag="div">
          <h1>{{item.title}}</h1>
          <div>总计激活数：{{item.number}}</div>
        </router-link>
      </li>
    </ul>
  </div>
</template>
<style scoped>
  .zhTitle{
    font-size: 16px;
  }
  .zh-project-detail{
    width:200px;
    height:150px;
    text-align: center;
    float: left;
    background: #f0f2f7;
    display: block;
    cursor: pointer;
    border: 1px solid transparent;
    margin-right: 20px;
    box-shadow: 0px 0px 1px 1px #ccc;
  }
  .zh-project-detail:hover{
    border: 1px solid blue;
  }
</style>
<script>
  import {mapActions} from 'vuex'
  export default{
    data (){
      return {
        items:[
          {title:'第一个项目',number:2645225},
          {title:'第二个项目',number:300},
          {title:'第三个项目',number:500}
        ],
      }
    },
    methods:{
      ...mapActions(['setInfo']),
      showName(str){
        var info=this.items[str]
        this.setInfo(info)
      }
    }
  }
</script>



