

export default {
  state:JSON.parse(sessionStorage.getItem('user')),
  mutations: {
    logIn(state){

    },
    logOut(state){
      sessionStorage.removeItem('user')
    }
  },
  actions: {
    logIn({commit},user) {
      commit('logIn',user)
    },
    logOut({commit}){
      commit('logOut')
    }
  }
}



