<template>
  <div id="nav">
    <ul class="nav-left">
      <li class="nav-item" v-for="(item,index) in items ">
        <router-link :to="'/'+item.ad" tag="div" active-class="zh-actived">{{item.name}}</router-link>
      </li>
    </ul>
  </div>
</template>
<script>
  export default{
    data (){
      return {
        items:[
          {name:'项目',ad:'project'},
          {name:'财务',ad:'money'}
        ]
      }
    }
  }
</script>
<style scoped>
  #nav{
    position: fixed;
    top:70px;
    bottom:0;
    left:0;

    background-color: #f0f2f7;
    overflow-y: auto;
    z-index: 15;


  }
  .nav-left {
    width:250px;
    font-family: "Microsoft Yahei", sans-serif;
    height: 50px;
    line-height: 50px;
    text-align: center;
    padding: 0;
  }
  .zh-actived{
    background:#d2dce6;

  }

</style>



