<template>
  <div id="login">
    <el-row >
      <el-col :span="24" class="zh-title zh-title-h1"><h1>TRIPICIS</h1></el-col>
      <el-col :span="24" class="zh-title-h2 "><h2>运营系统</h2></el-col>
      <el-col :span="8" class="zh-loginName">用户名</el-col>
      <el-col :span="8" class="zh-loginInput">
        <el-input placeholder="请输入用户名" v-model="nameInput"  autofocus="autofocus"></el-input>
      </el-col>
      <el-col :span="8" class="zh-loginName">密码</el-col>
      <el-col :span="8" class="zh-loginInput">
        <el-input placeholder="请输入密码" type='password' v-model="passInput"> </el-input>
      </el-col>
      <el-col :span="24" class="zh-showWrong" v-if="showWrong">
          用户密码错误
      </el-col>
      <el-col :span="24">
        <el-button :plain="true" class="zh-loginBtn" @click="toSubmit"><span>登录</span></el-button>
      </el-col>
    </el-row>
  </div>
</template>
<script>
  import {mapActions} from 'vuex'
  export default{
    data (){
      return {
        nameInput:'',
        passInput:'',
        showWrong:false
      }
    },
    methods: {
      ...mapActions(['logIn','enableLogin']),
      toSubmit() {
        if(this.nameInput !== this.$store.state.user.name ||
          this.passInput !== this.$store.state.user.password) {
          this.showWrong=true
        }else{
          this.enableLogin();
          this.$router.replace({path: '/project'});
        }
      }
    }
  }
</script>
<style scoped>
  body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,
  legend,input,button,textarea,p,blockquote,th,td { margin:0; padding:0; }
  #login{
    z-index: 101;
    width:800px;
    height: 500px;
    background-color: #2f4256;
    position:fixed;
    left: 50%;
    top: 50%;
    margin-left: -400px;
    margin-top: -250px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px 5px #888888;
    text-align: center;
    padding: 20px;
    color: #fff;
  }
  .zh-title{
    margin-bottom: 10px;
  }
  .zh-title-h1{
    margin-top: 50px;
    font-size: 25px;
  }
  .zh-title-h2{
    margin-bottom: 50px;
  }
  .zh-loginName{
    text-align: right;
    font-size: 16px;
    line-height: 30px;
    margin-bottom: 30px;
  }
  .zh-loginInput{
    margin-left: 50px;
    margin-bottom: 30px;
  }
  .zh-showWrong{
    color: red;
    position: absolute;
    bottom: 20px;
  }
  .zh-loginBtn{
    width: 80px;
    height: 40px;
    background-color: #21a1ff;
    border: none;
    color: #fff;
    position: relative;
    bottom: -40px;
  }

</style>



